<template>
  <div>
    <el-header style>
      <h1>SDplayer.club</h1>
    </el-header>
    <el-main>
      <el-row>
        <h1 class="font-weight-bold text-xs-center">SD敢达机体数据库</h1>
      </el-row>
      <el-row>
        <el-input placeholder="请输入内容" class="input-with-select" v-model="filterPost.nickname">
          <el-button slot="append" icon="el-icon-search" :loading="loading" @click="onfilterSearch"></el-button>
        </el-input>
      </el-row>
      <el-row>筛选器：
        <!-- tag显示已选 -->
        <el-tag
          v-if="RankChip"
          v-model="RankChip"
          closable
          type="warning"
          @close="filterPost.Rank = []"
        >
          <span v-for="(item , index) in filterPost.Rank" :key="index">
            {{item}}
            <span v-if="index != filterPost.Rank.length-1">,</span>
          </span>
        </el-tag>
        <el-tag v-if="spChip" v-model="spChip" closable @close="filterPost.special = []">
          <span v-for="(item , index) in filterPost.special" :key="index">
            {{item}}
            <span v-if="index != filterPost.special.length-1">,</span>
          </span>
        </el-tag>
      </el-row>
      <el-row>
        <el-col :span="24">Rank:</el-col>
        <el-col :span="24">
          <el-checkbox v-model="filterPost.Rank" label="S">S（SS,SR）</el-checkbox>
          <el-checkbox v-model="filterPost.Rank" label="A">A（AS,AR）</el-checkbox>
        </el-col>
        <el-col :span="24">
          <el-checkbox v-model="filterPost.Rank" label="B">B（BS,BR）</el-checkbox>
          <el-checkbox v-model="filterPost.Rank" label="C">C（CS,CR）</el-checkbox>
        </el-col>
      </el-row>
      <!-- 特色 -->
      special:
      <el-row>
        <el-checkbox
          v-for="(item, index) in filterMap.special"
          :key="index"
          v-model="filterPost.special"
          :label="item"
        >{{item}}</el-checkbox>
      </el-row>
      <el-row>扭蛋机：
        <el-select clearable v-model="filterPost.Machine" filterable placeholder="请选择">
          <el-option v-for="item in filterMap.Machine" :key="item" :label="item" :value="item"></el-option>
        </el-select>
      </el-row>
      <el-row>作战地形：
        <el-select clearable v-model="filterPost.landType" filterable placeholder="请选择">
          <el-option v-for="item in filterMap.landType" :key="item" :label="item" :value="item"></el-option>
        </el-select>
      </el-row>
      <el-row>攻击距离：
        <el-select clearable v-model="filterPost.fightType" filterable placeholder="请选择">
          <el-option v-for="item in filterMap.fightType" :key="item" :label="item" :value="item"></el-option>
        </el-select>
      </el-row>
      <br>
      <el-row height="300">
        <el-button type="primary" :loading="loading" @click="onfilterSearch">搜索</el-button>
        <el-button type="danger" @click="filterClear">清除条件</el-button>
      </el-row>

      <br>
    </el-main>
    <el-footer>
      <div>
        <el-button @click="AuthorBlock = !AuthorBlock">作者的话</el-button>

        <div>
          <el-collapse-transition>
            <div v-show="AuthorBlock">
              <p class="transition-box">建议吐槽，请发至作者邮箱：rabbit586@163.com</p>
              <p class="transition-box">数据库错误，请发给G-Joker：935954867@qq.com</p>
              <p>欢迎积极反馈，我会持续更新的！欢迎到SD敢达吧灌水^_^</p>
              <p>渣浪微博：@6dbm</p>
            </div>
          </el-collapse-transition>
        </div>
      </div>
    </el-footer>
  </div>
</template>

<script>
import axios from "axios";
export default {
  data() {
    return {
      AuthorBlock: false,
      loading: false,
      showFilter: true,
      filterPost: {
        Rank: [],
        special: [],
        fightType: "",
        landType: "",
        Machine: "",
        nickname: "",
        page: 1,
        pagesize: 10
      },
      filterMap: {
        Rank: ["S", "A", "B", "C"],
        special: ["变形", "修理", "大型", "狙击"],
        fightType: ["近距离", "中距离", "远距离"],
        landType: ["万能", "宇宙", "地面", "水下", "未知"],
        Machine: [
          "1号",
          "2号",
          "3号",
          "4号",
          "5号",
          "6号",
          "7号",
          "8号",
          "9号",
          "10号",
          "11号",
          "12号",
          "13号",
          "14号",
          "15号",
          "16号",
          "17号",
          "18号",
          "19号",
          "20号",
          "21号",
          "22号",
          "23号",
          "24号",
          "25号",
          "26号",
          "27号",
          "28号",
          "29号",
          "30号",
          "31号",
          "32号",
          "33号",
          "34号",
          "35号",
          "36号",
          "37号",
          "38号",
          "39号",
          "40号",
          "41号",
          "42号",
          "43号",
          "44号",
          "45号",
          "46号",
          "47号",
          "48号",
          "49号",
          "50号",
          "51号",
          "52号",
          "53号",
          "54号",
          "55号",
          "56号",
          "57号",
          "58号",
          "59号",
          "60号",
          "61号",
          "62号",
          "63号",
          "64号",
          "65号",
          "66号",
          "67号",
          "68号",
          "69号",
          "70号",
          "71号",
          "72号",
          "73号",
          "74号",
          "75号",
          "76号",
          "77号",
          "78号",
          "79号",
          "80号",
          "81号"
        ]
      },
      items: [
        {
          action: "local_activity",
          title: "Attractions",
          items: [{ title: "List Item" }]
        },
        {
          action: "restaurant",
          title: "Dining",
          active: true,
          items: [
            { title: "Breakfast & brunch" },
            { title: "New American" },
            { title: "Sushi" }
          ]
        },
        {
          action: "school",
          title: "Education",
          items: [{ title: "List Item" }]
        },
        {
          action: "directions_run",
          title: "Family",
          items: [{ title: "List Item" }]
        },
        {
          action: "healing",
          title: "Health",
          items: [{ title: "List Item" }]
        },
        {
          action: "content_cut",
          title: "Office",
          items: [{ title: "List Item" }]
        },
        {
          action: "local_offer",
          title: "Promotions",
          items: [{ title: "List Item" }]
        }
      ]
    };
  },
  computed: {
    RankChip: {
      get: function() {
        let rank = this.filterPost.Rank;
        if (rank.length) {
          return true;
        } else {
          return false;
        }
      },
      set: function() {
        this.filterPost.Rank = [];
      }
    },
    spChip: {
      get: function() {
        let SP = this.filterPost.special;
        if (SP.length) {
          return true;
        } else {
          return false;
        }
      },
      set: function() {
        this.filterPost.special = [];
      }
    }
  },

  methods: {
    filterClear() {
      this.filterPost = {
        Rank: [],
        special: [],
        fightType: "",
        landType: "",
        Machine: "",
        nickname: "",
        page: 1,
        pagesize: 10
      };
    },
    onfilterSearch() {
      let _this = this;
      this.loading = true;
      _this.$emit("finding", true);
      axios
        .post(`/getGundam/FilterSearch`, this.filterPost)
        .then(res => {
          // console.log(res);
          _this.$emit("filterSearchData", res);
          _this.$emit("finding", false);
          _this.loading = false;
          // _this.gundams = _this.gundams.concat(res.data);
        })
        .catch(function(error) {
          _this.loading = false;
          // console.log(error);
        });
    }
  }
};
</script>

<style>
.search-row {
  /* height: 50px; */
}
