<template>
  <div>
    <el-row>
      <el-col :span="24">
        <el-pagination
          :background="true"
          layout="total ,prev, pager, next, jumper"
          :total="postData.searchNum"
          :current-page="postData.page"
          @current-change="onPageChange"
        ></el-pagination>
      </el-col>
      <el-col :span="24">
        <el-table
          :data="gundams"
          style="width: 100%"
          :show-header="false"
          :row-style="{height:50}"
          v-loading="listLoading"
        >
          <el-table-column prop="avatarURL" label="avatarURL" width="180" size="small">
            <template slot-scope="scope">
              <a @click="openDialog(scope.row)" href="#">
                <div class="img-responsive">
                  <img :src="scope.row.avatarURL">
                </div>
              </a>
            </template>
          </el-table-column>
          <el-table-column prop="model" label="model" width="180"></el-table-column>
          <el-table-column prop="rank" label="rank" width="100"></el-table-column>
          <el-table-column prop="rating" label="rating" width="100"></el-table-column>
          <el-table-column label="能力值">
            <template slot-scope="scope">
              <table>
                <tr>
                  <td>攻击</td>
                  <td>防御</td>
                  <td>机动</td>
                  <td>操控</td>
                  <td>4D综合</td>
                  <td>3D综合</td>
                </tr>
                <tr>
                  <td>{{scope.row.att}}</td>
                  <td>{{scope.row.def}}</td>
                  <td>{{scope.row.fly}}</td>
                  <td>{{scope.row.ctl}}</td>
                  <td>{{scope.row.D4sum}}</td>
                  <td>{{scope.row.D3sum}}</td>
                </tr>
              </table>
            </template>
          </el-table-column>
        </el-table>
      </el-col>
      <el-col :span="24">
        <el-pagination
          :background="true"
          layout="total ,prev, pager, next, jumper"
          :total="postData.searchNum"
          :current-page="postData.page"
          @current-change="onPageChange"
        ></el-pagination>
      </el-col>
    </el-row>

    <ddialog :show="showDialog" @dialogData="closeDialog" :gundam="detailRobot"></ddialog>
  </div>
</template>

<script>
import ddialog from "./dialog";
import axios from "axios";
// var BaseURL = 'http://132.232.68.171';
var BaseURL = "http://localhost:3001";

export default {
  components: {
    ddialog: ddialog
  },
  data: () => ({
    showDialog: false,
    loading: false,
    detailRobot: {},
    gundams: [],
    pageInfo: {
      page: 1,
      pagesize: 10
    },
    postData: {
      page: 1,
      pagesize: 10,
      searchNum: 767
    }
  }),
  props: {
    GundataRes: Object,
    filterSearchStart: Boolean
  },
  watch: {
    GundataRes: function(newVal, oldVal) {
      // console.log(newVal);
      this.gundams = newVal.data.res;
      (this.pageInfo = {
        page: newVal.data.postData.page,
        pagesize: newVal.data.postData.page
      }),
        (this.postData = newVal.data.postData);
    }
  },
  computed: {
    listLoading: {
      get: function() {
        return this.loading && !this.filterSearchStart;
      },
      set: function(val) {
        this.loading = this.filterSearchStart = val;
      }
    }
  },
  methods: {
    closeDialog(data) {
      this.showDialog = data; //子组件触发父组件事件，进行数据变更，同步result数据
    },
    openDialog(gundam) {
      this.showDialog = true;
      this.detailRobot = gundam;
    },
    loadMore() {
      let _this = this;
      this.busy = true;
      //把busy置位true，这次请求结束前不再执行
      setTimeout(() => {
        _this.pageInfo.page++;
        _this.loadPage();
        //调用获取数据接口，并且传入一个true，让axios方法指导是否需要拼接数组。
      }, 500);
    },
    loadPage() {
      let _this = this;
      let page = _this.pageInfo.page,
        pagesize = _this.pageInfo.pagesize;
      this.axios
        .get(`/getGundam?pagesize=${pagesize}&page=${page}`)
        .then(res => {
          // console.log(res);
          _this.gundams = _this.gundams.concat(res.data);
        });
    },
    onPageChange(val) {
      let _this = this;
      _this.loading = true;
      _this.postData.page = val;
      let pagesize = _this.postData.pagesize;
      this.axios
        .get(`/getGundam?pagesize=${pagesize}&page=${val}`)
        .then(res => {
          // console.log(res);
          _this.gundams = res.data;
          _this.loading = false;
        });
    }
  },
  mounted() {
    this.loadPage();
  }
};
</script>

<style>
.img-responsive {
  max-width: 100%;
  height: 110px;
  padding-bottom: 10%;
  overflow: hidden;
}
.img-responsive img {
  width: 70%;
}
.el-table .warning-row {
  background: oldlace;
}

.el-table .success-row {
  background: #f0f9eb;
}
</style>
