<template>
  <div id="app">
    <el-container>
      <el-aside width="25%" hidden-sm-and-down>
        <searchBlock @filterSearchData="searchDataRes" @finding="finding"></searchBlock>
      </el-aside>
      <el-container>
        <RobotList :GundataRes="GundataRes" :filterSearchStart="filterSearchStart"></RobotList>
      </el-container>
    </el-container>
  </div>
</template>

<script>
import RobotList from "./components/RobotList";
import searchBlock from "./components/searchBlock";

export default {
  name: "app",
  components: {
    RobotList: RobotList,
    searchBlock: searchBlock
  },
  data() {
    return {
      GundataRes: {},
      filterSearchStart: false
    };
  },
  methods: {
    searchDataRes(res) {
      let _this = this;
      _this.GundataRes = res;
    },
    finding(start){
      this.filterSearchStart = true;
    }
  }
};
</script>

<style>
#app {
  font-family: "Avenir", Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
}
</style>
